<template>
  <view class="page">
    <uni-forms :value="user_address" ref="form">

      <uni-forms-item label="收货人" name="receiver">
        <uni-easyinput type="text" v-model="user_address.receiver" placeholder="请输入收货人"
          placeholder-style="color:#C0C8D3" />
      </uni-forms-item>

      <uni-forms-item label="手机号" name="mobile">
        <uni-easyinput type="number" v-model="user_address.mobile" placeholder="请输入手机号"
          placeholder-style="color:#C0C8D3" />
      </uni-forms-item>


      <uni-forms-item label="所在地区" name="area">
        <uni-data-picker v-model="user_address.area" :localdata="items" popup-title="请选择地区" @change="areaChange"
          @nodeclick="onnodeclick">
        </uni-data-picker>
      </uni-forms-item>

      <uni-forms-item label="街道地址" name="add_str">
        <uni-easyinput type="text" v-model="user_address.add_str" placeholder="请输入街道地址"
          placeholder-style="color:#C0C8D3" />
      </uni-forms-item>

    </uni-forms>

    <uni-forms-item label="是否默认" name="type">
      <label class="radio" @click="deaultChange">
        <image v-if="!is_checked" src="/static/images/default.png" mode=""></image>
        <image v-else src="/static/images/default_active.png" mode=""></image>
      </label>
    </uni-forms-item>


    <button @click="submitForm">保存</button>


  </view>
</template>

<script>
  export default {
    data() {
      return {
        user_address: '',
        is_checked: true,
        items: [{
            text: "一年级",
            value: "1-0",
            children: [{
                text: "1.1班",
                value: "1-1"
              },
              {
                text: "1.2班",
                value: "1-2",
                children: [{
                  text: '1区',
                  value: '111'
                }, {
                  text: '2区',
                  value: '222'
                }, {
                  text: '3区',
                  value: '333'
                }]
              }
            ]
          },
          {
            text: "二年级",
            value: "2-0"
          },
          {
            text: "三年级",
            value: "3-0"
          }
        ]
      };
    },
    onLoad() {
      this.user_address = JSON.parse(uni.getStorageSync('user_address') || "{}")
    },
    methods: {
      deaultChange() {
        this.is_checked = !this.is_checked
      },
      areaChange(e) {
        console.log(e.detail.value)
      }
    }
  }
</script>

<style lang="scss">
  .page {
    padding-top: 20rpx;
  }

  .uni-forms-item {
    background-color: #FFFFFF;
    border-bottom: 1px solid #F6F6F6;

    .uni-forms-item__inner {
      padding: 30rpx !important;
    }

    .uni-forms-item__label .label-text.data-v-61dfc0d0 {
      font-size: 30rpx;
      font-weight: bold;
      color: #000104;
    }

    .uni-easyinput__content-input.data-v-abe12412 {
      font-size: 30rpx;
    }

    .is-input-border {
      border: none !important;
    }

    .input-value-border {
      border: unset !important;
    }

    .input-value.data-v-3ed22fe0,
    .uni-data-tree.data-v-3ed22fe0 {
      font-size: 30rpx !important;
    }

    .placeholder.data-v-3ed22fe0 {
      color: #c0c8d3;
      padding-left: 5px;
    }

    .radio {
      image {
        width: 45px;
        height: 20px;
        margin-top: 8px;
        margin-left: 10px;
      }
    }
  }

  button {
    height: 100rpx;
    margin-top: 30rpx;
    line-height: 100rpx;
    font-size: 30rpx;
    font-weight: bold;
    // border: none;
    color: #294D7C;
    background: #FFFFFF;
    border-radius: 0;
  }

  button::after {
    border: none;
  }
</style>
